header {
    margin-bottom: 73px;
}

.heder-bacgraund {
    background: url("../img/header/bg-header.png") top 0px center no-repeat;
    min-height: 700px;
}

.header-wraper {

    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 1370px;
    height: 107px;
    box-shadow: 0px 6px 37.4px 5.6px rgba(220, 204, 252, 0.43);
    margin: 0 auto;

}

.logo {
    margin-left: 7%;
}

.header-social-fb {
    font-size: 26px;
    color: #6f41b7;
}

.header-social-ins {
    font-size: 26px;
    color: #6f41b7;
}

nav {
    margin-left: 5%;
}

nav>ul>a>li {
    display: inline-block;
    border: 1px solid #6f41b7;
    border-radius: 3px;
    padding: 17px 28px;
    margin-left: -4.7px;
    text-transform: uppercase;
    font-weight: bold;
    color: #6b7893;
    font-size: 16px;
}

.header-nav-li:hover {
    background-color: #6f41b7;
    color: #fff;
}

.header-phone>a {
    max-width: 145px;
    height: 16px;
    font-size: 18px;
    font-weight: bold;
    line-height: 1.5;
    text-align: left;
    color: #484b68;
    margin-left: 53px;

}

.header-phone1>a {
    max-width: 145px;
    height: 16px;
    font-size: 18px;
    font-weight: bold;
    line-height: 1.5;
    text-align: left;
    color: #484b68;
    margin-left: 53px;
    display: none;

}

.header-social-ins {
    font-weight: bold;
}

.header-flag {
    margin-left: 30px;

}

.flag-icon-ru {
    display: none;
}

.header-icon-caret {
    padding-left: 12px;
    color: #712cb4;
}

.header-social {
    margin: 0 10px;
}

.header-txt-title {
    margin: 29px 0 29px 0;

    h1 {
        height: 37px;
        font-size: 36px;
        font-weight: 800;
        line-height: 1.32;
        text-align: left;
        color: #484b68;
        padding-left: 40px;
    }
}

.header-txt-one {
    max-width: 469px;
    display: inline-block;
    margin-left: 34px;
    font-size: 15px;
    line-height: 1.47;
    text-align: left;
    color: #484b68;
    vertical-align: top;

    p {
        margin-bottom: 20px;
    }

    .header-txt-bottom {
        margin-bottom: 460px;
    }
}


// ---------------------------------burger menu --------------------------------


.menu-link {
    display: none;
    margin: 7px;
    width: 50px;
    height: 50px;
    background: #fff;
    border-radius: 50%;
    box-shadow: 1px 1px 10px rgba(0, 0, 0, .4);
    position: fixed;
    top: 0px;
    left: 20px;
    z-index: 11;
}


.menu-li {
    margin-top: 240px;
}

.menu-link span,
span:before,
span:after {
    display: block;
    height: 2px;
    width: 18px;
    position: absolute;
    left: 50%;
    margin-left: -9px;
    background: #333;
    top: 50%;
    margin-top: -1px;

}

.menu-link span:before {
    transition: all 0.2s;
    content: '';
    margin-top: -6px;
}

.menu-link span:after {
    transition: all 0.2s;
    content: '';
    margin-top: 4px;
}

.menu-link_active span {
    background-color: transparent;
}

.menu-link_active span:before {

    transform: rotate(135deg);
    margin-top: 0;
}

.menu-link_active span:after {

    transform: rotate(-135deg);
    margin-top: 0;
}

.menu {
    opacity: .9;
    background-color: #6f41b7;
    position: fixed;
    width: 100vw;
    height: 200%;
    z-index: 8;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.4s;
    left: -100vw;
}

.menu a {
    display: block;
    width: 100%;
    font-weight: 600;
    font-size: 17px;
    letter-spacing: 3px;
    line-height: 3;
    color: #fff;
    transition: all 0.2s;
}

.menu a:hover {
    background-color: rgba(240, 237, 237, 0.1);
    text-decoration: none;
}

.menu ul li {
    width: 100vw;
}

.menu ul {
    list-style: none;
    padding: 0;
    text-align: center;
}

.menu_active {
    left: 0;
}

// -------------------------end burger menu ------------------------------------


.heder-left-title {
    position: fixed;
    right: 215px;
    margin-top: 101px;
    text-align: right;
    text-transform: uppercase;
    font-size: 14px;
    color: #484b68;

}


//------------------------- медиа Адаптив -----------------------------------------

@media (max-width: 1390px) {

    .heder-left-title {
        margin-left: 1030px;

    }

}

@media (max-width: 1350px) {
    .logo {
        margin-left: 3%;
    }

    nav {

        margin-left: 3%;
    }

    .header-phone>a {
        height: 16px;
        font-size: 16px;
        margin-left: 1em;
    }



    .heder-left-title {
        display: none;
    }
}

@media (max-width: 1150px) {


    .header-phone1>a {
        display: block;
        font-size: 16px;
        margin-left: -10px;
        width: 145px;
        margin-bottom: 8px;
        padding-left: 10px;
    }

    .header-phone>a {
        display: none;
    }

    .header-icon-caret {
        margin-left: -103px;
        margin-top: 14px;
    }

    .header-social {
        padding-top: 28px;
    }

}

@media (max-width: 1050px) {
    .header-wraper {
        justify-content: start;
    }

    nav {
        display: none;
    }

    .header-txt-title h1 {
        // width: 794px;
        height: 37px;
        font-size: 32px;
        font-weight: 800;
        line-height: 1.32;
        text-align: center;
        color: #484b68;
        padding: 0;
    }

    .header-section-txt {
        text-align: center;
    }

    .header-txt-one {
        max-width: 100%;
    }

    .menu-link {
        display: block;
        margin-left: 75%;
        margin-top: 29px;

    }

}

@media (max-width: 640px) {
    .header-txt-title h1 {

        font-size: 29px;
    }

}

@media (max-width: 580px) {
    .heder-bacgraund {
        background-size: cover;
    }

    .header-txt-title h1 {

        margin-bottom: 40px;
    }

    .logo>a>img {
        width: 75px;
    }

    .plan-title h2 {
        margin: 0 auto;
        text-align: center;
    }

    .header-phone1>a {

        font-size: 14px;
    }

    .header-phone1>a {
        width: 121px;
    }

    .header-icon-caret {
        padding-left: 30px;
    }


}

@media (max-width: 370px) {
    .header-txt-title h1 {
        font-size: 29px;
        margin-bottom: 80px;
    }

}