@import 'mixins';

.plan-blocks-item {
    display: flex;
    min-width: 150px;
    max-width: 340px;
    align-items: center;
    margin-bottom: 60px;
    margin-right: 20px;

}

.plan-blocks-bg {
    background-image: url("../img/plan/circle.png");
    background-repeat: no-repeat;
    background-position: center center;
    min-width: 67px;
    min-height: 62px;
}

.plan-blocks {
    display: flex;
    flex-wrap: wrap;

}

.plan-title>h2 {
    font-size: 30px;
    color: #484b68;
    margin-bottom: 50px;
    text-transform: uppercase;
    font-weight: 900;
}

.plan-title>h2>span {
    color: #f9bf00;
}

.plan-blocks-bg-txt {
    display: inline-block;
    font-size: 24px;
    font-weight: 900;
    color: #fff;
    padding-top: 20px;
    padding-left: 20px;
}

.plan-blocks-txt {
    font-size: 16px;
    color: #6b7893;
    line-height: 1.5;
    margin-left: 16px;
}

.plan-txt-bottom {
    font-size: 16px;
    color: #9845e1;
    border-left: 5px solid #f9bf00;
    padding-left: 10px;
    max-width: 996px;
    margin-bottom: 46px;
    line-height: 1.5;
}

.plan-btn-wrap {

    margin: 0 auto;

    .plan-btn {
        text-align: center;
        padding-top: 12px;
        line-height: 1.5;
        font-size: 16px;
        color: #fff;

        @include btn;
    }

    .plan-btn:hover {
        background-image: linear-gradient(to top, #FC6E1F, #F9BC01);
        animation: planBtn .7s ease-in .1s 1 normal;
    }

    @keyframes planBtn {

        0% {
            transform: rotateX(0deg);
        }

        100% {
            transform: rotateX(360deg);
        }
    }
}

@media (max-width: 1100px) {
    .plan {
        max-width: 750px;
        margin-top: -100px;
    }
}

@media (max-width: 750px) {
    .plan {
        max-width: 550px;
        margin-top: -100px;
    }

    .plan-blocks {
        margin-left: auto;
        margin-right: auto;
        display: block;
        max-width: 312px;
    }
}

@media (max-width: 723px) {
    .plan {
        max-width: 550px;
        margin-top: -180px;
    }
}

@media (max-width: 580px) {
    .plan {
        max-width: 550px;
        margin-top: 80px;
    }
}